import { DateAlfresco } from '../../content-custom-api/model/dateAlfresco';

export class TaskUpdateRepresentation {
    category?: string;
    categorySet?: boolean;
    description?: string;
    descriptionSet?: boolean;
    dueDate?: Date;
    dueDateSet?: boolean;
    formKey?: string;
    formKeySet?: boolean;
    name?: string;
    nameSet?: boolean;
    parentTaskId?: string;
    parentTaskIdSet?: boolean;
    priority?: number;
    prioritySet?: boolean;

    constructor(input?: any) {

        if (input) {
            Object.assign(this, input);
            this.dueDate = input.dueDate ? DateAlfresco.parseDate(input.dueDate) : undefined;
        }
    }

}
